<template>
  <echart :option="option" />
</template>
<script>
import Echart from "@/components/Echart.vue";

export default {
  mounted() {
    this.$unlock();
  },
  data() {
    return {
      option: {
        title: {
          show: false
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          bottom: 10,
          left: "center",
          data: ["其他", "供应商", "信号"]
        },
        series: [
          {
            name: "事件比例",
            type: "pie",
            radius: "60%",
            center: ["50%", "50%"],
            data: [
              {
                name: "其他",
                value: Math.round(Math.random() * 100000)
              },
              {
                name: "供应商",
                value: Math.round(Math.random() * 100000)
              },
              {
                name: "信号",
                value: Math.round(Math.random() * 100000)
              }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      }
    };
  },
  components: {
    Echart
  }
};
</script>
<style lang="less" scoped>
.event-count {
  position: relative;

  > ul {
    position: absolute;
    display: flex;

    li {
      flex: 1;
    }
  }
}
</style>